.app {
  max-width: 724px;
  margin: 30px auto;
}

.sdc-article-related-stories__content {
  position: relative;
}

.sdc-article-related-stories--standard > .sdc-article-related-stories__content,
.sdc-article-related-stories--climate-change > .sdc-article-related-stories__content,
.sdc-article-related-stories--no-image > .sdc-article-related-stories__content {
  border: none !important;
  padding: 0 !important;
}

.sdc-article-related-stories--border-full > .sdc-article-related-stories__content,
.sdc-article-related-stories--background > .sdc-article-related-stories__content,
.sdc-article-related-stories--no-image-full-border > .sdc-article-related-stories__content,
.sdc-article-related-stories--no-image-background > .sdc-article-related-stories__content {
  padding-right: 24px !important;
  padding-left: 24px !important;
  border: 1px solid #eaeaea !important;
  border-radius: 5px;
}

.sdc-article-related-stories--background > .sdc-article-related-stories__content,
.sdc-article-related-stories--no-image-background > .sdc-article-related-stories__content {
  border: none !important;
  background: linear-gradient(180deg ,#fafafa,#f1f2f2);
}

.sdc-article-related-stories__title {
  font-family: 'Sky Headline' !important;
  font-size: 30px !important;
  line-height: 1 !important;
  text-transform: uppercase;
  font-weight: 400 !important;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  margin-bottom: 24px;
}

.sdc-article-related-stories--climate-change .sdc-article-related-stories__title {
  background: linear-gradient(80.26deg,#288b96 32.35%,#0a63b3 85.46%) !important;
  color: #fff;
  padding-left: 0.5em;
  padding: 10px;
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
}

.sdc-article-related-stories--climate-change .sdc-article-related-stories__title:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3rem;
  top: -34px;
  left: 0;
  background: linear-gradient(347.32deg,hsla(0,0%,100%,.25) 30.64%,hsla(0,0%,100%,0) 78.74%);
  mix-blend-mode: overlay;
  opacity: .6;
  transform: skewY(-1.5deg);
  z-index: 9999;
}

.sdc-article-related-stories__title:before {
  content: "";
  width: 8px;
  height: 25px;
  background: linear-gradient(180deg,#164c99,#032c77);
  margin-right: 0.5em;
}

.sdc-article-related-stories__item:last-child {
  margin-bottom: 0 !important;
}

.sdc-article-related-stories__item {
  width: 100%;
}

.sdc-article-related-stories--no-image .sdc-article-related-stories__image,
.sdc-article-related-stories--no-image-border .sdc-article-related-stories__image,
.sdc-article-related-stories--no-image-full-border .sdc-article-related-stories__image,
.sdc-article-related-stories--no-image-background .sdc-article-related-stories__image {
  display: none;
}

.sdc-article-related-stories__image {
  width: 125px;
}

.sdc-article-related-stories__view-more {
  position: absolute;
  right: 24px;
  top: 26px;
  color: #114793;
  font-weight: 600;
}

.sdc-article-related-stories__view-more:hover {
  cursor: pointer;
  text-decoration: underline;
}

@media (max-width: 480px) {
  .sdc-article-related-stories__image {
    display: none !important;
  }
}